import React, { Component } from 'react'
import GaugeChart from 'react-gauge-chart'

export default class Skills extends Component{
    render(){
        return (

            <div className="container-fluid py-5" id="skill">
              <div className="container">
                <div className="position-relative d-flex align-items-center justify-content-center">
                  <h1 className="display-1 text-uppercase text-white" style={{WebkitTextStroke: '1px #dee2e6'}}>Skills</h1>
                  <h1 className="position-absolute text-uppercase text-primary">My Skills</h1>
                </div>
                <div className="row align-items-center">
                  
                   <Skill1></Skill1>
                   <Skill1></Skill1>
                   <Skill1></Skill1>
                   <Skill1></Skill1>
                   <Skill1></Skill1>
                   <Skill1></Skill1>  
                  
                </div>
              </div>
            </div>
          );
    }
}

export class Skill1 extends Component{
    render(){
        return(
          <div className="col- 2">
            
            <GaugeChart id="gauge-chart2" className='skill-percent'
  nrOfLevels={20} 
  percent={0.86} 
  hideText ={false}
  textColor={'#000'}
/>
          </div>

          
        )
    }
}